import React  from 'react'
import './navbar.css'

class NavBar extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            hamBurgerMenuHidden: true
        }
    }

    hamBurgerMenu = () => {
        const currentState = this.state.hamBurgerMenuHidden;
        this.setState({ hamBurgerMenuHidden: !currentState })
    }
    render() {
        return(
            <div className='navbar-image'>
                <p></p>
                <div className='navbar-menu' >
                    <ul>
                        <li id='menu-links'><a href="javascript:void(0);" onClick={this.hamBurgerMenu}><i class="fa fa-bars"></i></a></li>
                        {/* <li id='home-logo'><a href="#home"><i class="fa fa-film" font-size={80}></i> FILMLYSIS </a></li> */}
                        <div className={this.state.hamBurgerMenuHidden? 'menu-items-hidden' : 'menu-items-display'}>
                            <li><a href="#home">CLOSE UP </a></li>
                            <li><a href="#home">EDITS </a></li>
                            <li><a href="#home">CAMERA </a></li>
                            <li><a href="#home">DEBUTS AND FINALE </a></li>
                        </div>
                    </ul>
            </div>
            </div>
        );
        
    }
}

export default NavBar